import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ButtonBase from '@material-ui/core/ButtonBase';
import Typography from '@material-ui/core/Typography';
import icorredor from '../images/corredornatural/riera.jpeg'; // with import
import icontaminacio from '../images/corredornatural/contaminacio1.jpg'; // with import
import icanyars from '../images/corredornatural/canyars.jpg'; // with import

const options = [
  {
    url: icorredor,
    title: "Desaparició del corredor biologic",
    id: 'corredor',
    width: '34%',
  },
  {
    url: icontaminacio,
    title: 'Més Contaminacio',
    id: 'contaminacio',
    width: '33%',
  },
  {
    url: icanyars,
    title: 'Més presió a la riera dels canyars',
    id: 'riera',
    width: '33%',
  },
];

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    minWidth: 300,
    width: '100%',
  },
  image: {
    position: 'relative',
    height: 200,
    [theme.breakpoints.down('xs')]: {
      width: '100% !important', // Overrides inline-style
      height: 100,
    },
    '&:hover, &$focusVisible': {
      zIndex: 1,
      '& $imageBackdrop': {
        opacity: 0.1,
      },
      '& $imageMarked': {
        height: 2,
        width: 98,
        backgroundColor: 'white',
        position: 'absolute',
        bottom: -2,
        left: 'calc(50% - 49px)',
        opacity:'0.85',
        transition: theme.transitions.create('opacity'),
      },
      '& $imageTitle': {
        textShadow: '1px 1px 2px black, 0 0 1em black, 0 0 0.2em black',
        opacity:'0.85',
      },
    },
  },
  focusVisible: {},
  imageButton: {
    position: 'absolute',
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    color: 'theme.palette.common.white',
  },
  imageSrc: {
    position: 'absolute',
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    backgroundSize: 'cover',
    backgroundPosition: 'center 40%',
  },
  imageBackdrop: {
    position: 'absolute',
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    backgroundColor: theme.palette.common.black,
    opacity: 0.7,
    transition: theme.transitions.create('opacity'),
  },
  imageTitle: {
    position: 'relative',
    padding: `${theme.spacing(2)}px ${theme.spacing(4)}px ${theme.spacing(1) + 6}px`,
    color: '#fff',
    opacity:'0.75',

  },
  imageMarked: {

  },
}));

export default function ButtonBases() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      {options.map(image => (
        <ButtonBase
          focusRipple
          key={image.title}
          className={classes.image}
          focusVisibleClassName={classes.focusVisible}
          style={{
            width: image.width,
          }}
        >
        <span
          className={classes.imageSrc}
          style={{backgroundImage: `url(${image.url})`}}
          />
          <span className={classes.imageBackdrop} />
          <span className={classes.imageButton}>
            <Typography
              component="span"
              variant="subtitle1"
              color="inherit"
              className={classes.imageTitle}
            >
              {image.title}
              <span className={classes.imageMarked} />
            </Typography>
          </span>
        </ButtonBase>
      ))}
    </div>
  );
}
