import React from 'react';
import Grid from "@material-ui/core/Grid";
import { TwitterTimelineEmbed } from 'react-twitter-embed';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import icalamot from './images/vida/calamothead.jpg'; // with import
import iCartell from './images/cartell.png'; // with import
import ililCartell from './images/lilcartell.png'; // with import
import iCartello from './images/cartell.jpg'; // with import

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCircle } from '@fortawesome/free-regular-svg-icons';
import './App.css';
import { makeStyles } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';

const icircle = <FontAwesomeIcon color="#4b8179" icon={faCircle}  size="1x"/>;

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    backgroundColor: '#3e3e3e',
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
}));


function Calamot () {
  const matches = useMediaQuery('(min-width:600px)');
  const classes = useStyles();
  return (
    <React.Fragment>
      <Grid container spacing={3}>
      <Grid item lg={6} md={6} sm={12}>
            <img src={matches ? iCartell : ililCartell  }/>
        </Grid>
        <Grid item lg={6} md={6} sm={12}>
          <p>El Pla de Ponent és un pla urbanístic impulsat per l’Ajuntament de Gavà, té com a objectiu urbanitzar les zones del turó del Calamot,
          riera de Canyars i pla de Queralt; espai que fa de connectror biològic entre el Parc Natural del Garraf i el Delta del Llobregat, un indret insòlit on els
          visitants habituals conviuen i cuiden un hàbitat molt ric en patrimoni natural i històric.<br/><br/>
          El pla va néixer l'any 1993 després de que els terrenys
          fossin comprats per la constructora Vèrtix en plena bombolla immobiliària, i l'any 2006 es va aprovar el projecte, però durant l’esclat de la crisi es
          van aturar les obres. Ara, el fons d’inversió americà Kronos ha adquirit els terrenys sota el nom de la constructora Gavaland Development, S.A. i
          pretén iniciar les obres immediatament.<br/>
          Els primers efectes del Pla ja són visibles amb la construcció dels nous habitatges del barri de Can Ribes (els pisos i el Mercadona nou a l’entrada de Gavà):
          </p>
        </Grid>
        <Grid item xl={8} lg={8} md={8} sm={8}  xs={12} >
          <List component="nav" aria-label="Impacte del Pla de Calamot" >

            <ListItem button>
              <ListItemIcon>
                {icircle}
              </ListItemIcon>
              <ListItemText primary="Amb l’elaboració del Pla de Ponent Gavà, Castelldefels i el barri de la Sentiu quedaran units en un mateix continuum urbà." />
            </ListItem>

            <ListItem button>
              <ListItemIcon>
                {icircle}
              </ListItemIcon>
              <ListItemText primary="S’urbanitzarà la muntanya i la zona agrícola situades entre la carretera Santa Creu de Calafell, la carretera de la Sentiu i els pobles de Gavà i Castelldefels i provocant la destrucció d'un dels darrers pulmons de la zona, éssent aquesta una de les zones amb l'aire més contaminat d'Espanya." />
            </ListItem>

            <ListItem button>
              <ListItemIcon>
                {icircle}
              </ListItemIcon>
              <ListItemText primary="La destrucció de la muntanya i la construcció de 4896 nous habitatges en un espai de 200 hectàrees no suposa un model de ciutat sostenible. És pura especulació immobiliària amb què la constructora pretén enriquir-se amb la connivència d’un Ajuntament amb aires de grandesa que vol una ciutat cada cop més i més gran." />
            </ListItem>

            <ListItem button>
              <ListItemIcon>
                {icircle}
              </ListItemIcon>
              <ListItemText primary="Tot això pretén construir-se sense solucionar la gestió del clavegueram (problema que es veurà agreujat amb l’arribada de 12.000 nous veïns), on la manca de separació d’aigües residuals i de pluja provoca, en època de pluja intensa, la sortida d’aigües negres directament al mar, provocant molts problemes als veins i veïnes de Gavà Mar i els banyistes de les platjes del Baix Llobregat." />
            </ListItem>

            <ListItem button>
              <ListItemIcon>
                {icircle}
              </ListItemIcon>
              <ListItemText primary="L’estimació de creixement demogràfic de 1034 habitants/any que planteja el projecte, és desproporcionada amb el creixement real de Gavà durant els últims 10 anys, amb una mitjana de 150 habitants/any. Aquest macroprojecte urbanístic no és necessari: Gavà no necessita un creixement quantitatiu, sinó millores qualitatives per a la ciutadania que abordi les necessitats d'habitatge i equipaments públics a través d'una política d'urbanisme que posi al centre la vida de les persones i la protecció del medi en què habitem a través d'un procés participatiu i transparent." />
            </ListItem>
          </List>
        </Grid>
        <Grid item xl={4} lg={4} md={4} sm={4}  xs={12} align="center"  >
          <h3 className="heading"> Segueix-nos al Twitter </h3>
          <TwitterTimelineEmbed
           sourceType="profile"
           screenName="SalvemElCalamot"
           options={{height: 600}}
           noHeader
         />
        </Grid>
      </Grid>
    </React.Fragment>
  );
}

export default Calamot;
