import React from "react";
import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import Grid from '@material-ui/core/Grid';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    overflow: 'hidden',
  },
  bw: {
    filter: 'grayscale(100%)',
    '&:hover': {
    filter: 'grayscale(0%)',
    },
  },
  ibw: {
    filter: 'grayscale(0%)',
    '&:hover': {
    filter: 'grayscale(100%)',
    },
  },
  modal: {

  display: 'flex',
  alignItems: 'center',
  alignContent: 'center',
  justifyContent: 'center',
  border: '5px',

  },
  paper: {
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  }
}));


function Gallery(props) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [key, setKey] = React.useState(0);

  const handleOpen = (i) => {
    setOpen(true);
    setKey(i);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div className={classes.root}>
      <GridList cellHeight={250} className={classes.gridList}>
        {props.data.map( (tile,i) => (
          <GridListTile key={tile.img} rows={tile.rows || 1}  cols={tile.cols || 1} index={i} onClick={() => handleOpen(i)} >
            <img src={tile.img} alt={tile.title} className={ props.bw ? null : props.ibw===i ? classes.ibw : classes.bw } />
          </GridListTile>
        ))}
      </GridList>
      <Modal
          aria-labelledby="transition-modal-title"
          aria-describedby="transition-modal-description"
          className={classes.modal}
          open={open}
          onClose={handleClose}
          closeAfterTransition
          BackdropComponent={Backdrop}
          BackdropProps={{
            timeout: 500,
          }}
        >

        <Fade in={open}>
          <div class="container" key={key} align="center" onClick={() => setOpen(false)} >
            <img src={props.data[key].img} alt={props.data[key].title}/>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}

export default Gallery;
