import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardMedia from '@material-ui/core/CardMedia';
import ButtonBase from './Buttonbase';
const useStyles = makeStyles({
  card: {
    maxWidth: '100%',
  },
  media: {
      height: 500,
  },
  newMedia: {
    border: "1px solid",
  },
  newMedia2: {
    border: "1px solid",
    webkitFilter: "grayscale(100%)",
    filter: "grayscale(100%)",
  },
  hid: {
  }
});

export default function MediaCard(props) {
  const [isgrey, togglegrey] = useState(props.grey);
  const grey = props.grey;
  const classes = useStyles();
/*  const content = (
      <CardContent classes={{root: classes.hid}}>
      <Button>Default</Button>

        <Typography gutterBottom variant="h5" component="h2">
          {props.title}
        </Typography>
        <Typography variant="body2" color="textSecondary" component="p">
          {props.content}
        </Typography>
      </CardContent>
  )*/
  const content3 = (
    <ButtonBase/>
  );
  return (
    <Card
      className={classes.card}
      onMouseEnter={() => {togglegrey(false)}}
      onMouseLeave={() => {togglegrey(true)}}
    >
      <CardActionArea>
        <CardMedia
          component="img"
          classes={ grey ? (isgrey ? { media: classes.newMedia2 } : {media: classes.newMedia} ) : {media: classes.newMedia }}
          className={classes.media}
          image={props.image}
          title={props.alt}
        />
      </CardActionArea>
      {props.content ? content3 : null}

    </Card>
  );
}
