import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepContent from '@material-ui/core/StepContent';
import StepIcon from '@material-ui/core/StepIcon';

import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import { faTree } from '@fortawesome/free-solid-svg-icons';

const iTree = <FontAwesomeIcon color="#4b8179" icon={faTree}  size="1x"/>;

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
  },
  button: {
    marginTop: theme.spacing(1),
    marginRight: theme.spacing(1),
  },
  actionsContainer: {
    marginBottom: theme.spacing(2),
  },
  resetContainer: {
    padding: theme.spacing(3),
  },
  over: {
    opacity: '50%',
    '&:hover': {
      opacity: '100%',
    },
  }
}));

function getSteps1() {
  return [
    `Acumulació de terrenys en mans de l'empresa Vertix`,
    `Presentació del primer plà Urbanistic`,
    'Aprobació del Pla de Carat',
    'Incendis',
    'Ministerial',
    '',
    '',
  ];
}

function getStepContent(step) {
  switch (step) {
    case 0:
      return `L'empresa Vertix comença a adquirir terrenys a la zona del Calamot, fins arribar a coptar el 40% de les 220 hectàrees urbanitzables.`;
    case 1:
      return `A la Fira d'Espàrrecs de Gavà, l'Ajuntament va mostrar una maqueta amb el projecte urbanístic del Pla de Carat. <br/>
      Encara no s'havia aprovat ni el Pla d'Actuació Urbanística (PAU) ni el Pla Parcial (PP)
      que l'havia de desenvolupar, però la maqueta figurava a l'estand oficial de l'Ajuntament, cosa que evidenciava la voluntat municipal d'impulsar la urbanització del sector.`;
    case 2:
      return `Try out different ad text to see what brings in the most customers,
              and learn how to enhance your ads using features like ad extensions.
              If you run into any problems with your ads, find out how to tell if
              they're running and how to resolve approval issues.`;
    default:
      return 'Pendent';
  }
}

function getSteps() {
  return [
    <h4>Inicis dels anys 80: Acumulació de terrenys en mans de l'empresa Vertix</h4>,
    <h4>Març de 1988: Presentació del primer plà Urbanistic</h4>,
    <h4>Març de 1991: Aprobació del Pla de Carat</h4>,
    <h4>Agost de 1991: Incendis </h4>,
    <h4> Març de 1992: Inclusió en un Pla ministerial de Vivenda</h4>
  ];
}
function getYear2() {
  return [<Typography>Inicis dels anys 80 </Typography>, <Typography>Març de 1988</Typography>, <Typography>Març de 1991</Typography>, <Typography> Agost de 1991 </Typography>, <Typography> Març de 1992 </Typography>];
}


export default function VerticalLinearStepper() {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const steps = getSteps();
  const steps1 = getSteps1();

  const handleNext = (i) => {
    console.log(i)
    setActiveStep(i + 1);
  };

  const handleBack = (i) => {
    setActiveStep(i - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };
  const handleGo = (event,i) => {
    setActiveStep(i);
  };
  return (
    <div className={classes.root}>
      <Stepper activeStep={activeStep} orientation="vertical">
        {steps.map((label, index) => (
          <Step key={label} onClick={(e) => { e.stopPropagation(); handleGo(e,index)}} >
            <StepLabel className={ index <= activeStep ? null : classes.over}>{label}</StepLabel>
            <StepContent>
              <Typography>{getStepContent(index)}</Typography>
              <div className={classes.actionsContainer}>
                <div>
                  <Button
                    disabled={activeStep === 0}
                    color="primary"
                    size="small"
                    onClick={(e) => {e.stopPropagation();handleBack(index)}}
                    className={classes.button}
                  >
                    {activeStep === steps.length - 1 ? null : 'Anterior '}
                  </Button>
                  <Button
                    variant="contained"
                    color="primary"
                    size="small"
                    onClick={(e) => { e.stopPropagation();handleNext(index)}}
                    className={classes.button}
                  >
                    {activeStep === steps.length - 1 ? 'Continuarà' : 'Seguent'}
                  </Button>
                </div>
              </div>
            </StepContent>
          </Step>
        ))}
      </Stepper>
      {activeStep === steps.length && (
        <Paper square elevation={0} className={classes.resetContainer}>
          <Typography color="primary" align="center">Ara depen de nosaltres aturar-ho</Typography>

        </Paper>
      )}
    </div>
  );
}
